<template>
  <div class="provider">
    <!--  -->

    <el-card class="box-card">
      <div slot="header" class="clearfix" >
        <el-button type="primary" class="button_wrap" @click="dialogFormVisible = true"></el-button>
        <el-button type="success" class="button_wrap" @click="dialogFormVisible3 = true"></el-button>
      </div>
    </el-card>
    <!-- -->
    <el-table
        :data="providerData"
        tooltip-effect="dark"
        border
        style="width: 100%">
        <el-table-column
          prop="id"
          label="id"
          width="100">
        </el-table-column>
        <el-table-column
          prop="name"
          label=""
          width="200">
        </el-table-column>
        <el-table-column
          prop="memo"
          label=""
          width="430"
          >
        </el-table-column>
        <el-table-column
          prop="category"
          label=""
          width="200"
          >

        </el-table-column>
        <el-table-column
          prop="balance"
          label="/"
          width="100">
        </el-table-column>
        <el-table-column
          prop="cost"
          label=""
          width="100">
        </el-table-column>
        <el-table-column
          fixed="right"
          label=""
          width="230">
          <template slot-scope="scope">
            <el-button @click="deleteProvider(scope.row)" type="danger" size="small"></el-button>
            <el-button @click="infoProviderCost(scope.row)" type="warning" size="small"></el-button>
          </template>
        </el-table-column>
    </el-table>
    <!-- alert -->
    <el-dialog title="" :visible.sync="dialogFormVisible">
        <el-form :model="form">
          <el-form-item label="" label-width="120px">
            <el-input v-model="form.name" auto-complete="off"></el-input>
          </el-form-item>
          <el-form-item label="" label-width="120px">
            <el-input
              type="textarea"
              :rows="2"
              placeholder=""
              v-model="form.memo">
            </el-input>
          </el-form-item>
          <el-form-item label="" label-width="120px">
            <el-select v-model="form.category" placeholder="" >
              <el-option label="" value=""></el-option>
              <el-option label="" value=""></el-option>
              <el-option label="" value=""></el-option>
            </el-select>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="dialogFormVisible = false"> </el-button>
          <el-button type="primary" @click="newProvider"> </el-button>
        </div>
      </el-dialog>

    <!-- alert -->
    <el-dialog title="" :visible.sync="dialogFormVisible2">
        <el-form :model="infoCostRow">
          <el-form-item label="" label-width="120px">
            <el-input v-model="infoCostRow.name" auto-complete="off"></el-input>
          </el-form-item>
          <el-form-item label="" label-width="120px">
            <el-input
              type="textarea"
              :rows="2"
              placeholder=""
              v-model="infoCostRow.memo">
            </el-input>
          </el-form-item>

          <el-form-item label="" label-width="120px" >
            <el-select v-model="infoCostRow.category" placeholder=""  >
              <el-option label="" value=""></el-option>
              <el-option label="" value=""></el-option>

            </el-select>
          </el-form-item>

          <el-form-item label="/" label-width="120px">
            <el-radio-group v-model="sumExist">
              <el-radio-button label="/"></el-radio-button>
              <el-radio-button label="/"></el-radio-button>
            </el-radio-group>
          </el-form-item>

          <el-form-item label="/" label-width="120px">
            <el-input v-model="newNum" auto-complete="off"></el-input>
          </el-form-item>

        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="cancelAlert"> </el-button>
          <el-button type="primary" @click="infoProviderAlert"> </el-button>
        </div>
      </el-dialog>

    <!-- alert -->
    <el-dialog title="" :visible.sync="dialogFormVisible3" >
      <el-form :model="providerForm">

        <el-form-item label="" label-width="80px">
          <el-select v-model="providerForm.name" filterable placeholder="">
            <el-option
              v-for="item in providerData"
              :key="item.id"
              :label="item.name"
              :value="item.name">
            </el-option>
          </el-select>
        </el-form-item>



        <el-form-item label="" label-width="80px">
          <el-input v-model="providerForm.total" style="width: 29%;"></el-input>
        </el-form-item>
        <el-form-item label="/" label-width="80px">
          <el-input v-model="providerForm.balance" style="width: 29%;"></el-input>
        </el-form-item>

      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible3 = false"> </el-button>
        <el-button type="primary" @click="applyProvider"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import url from './middle/url.js'
export default {
  name: 'provider',
  data () {
    return {
      providerUrl: url.providerUrl,
      providerData: [],
      dialogFormVisible: false,
      dialogFormVisible2: false,
      dialogFormVisible3: false,
      infoCostRow: {},
      sumExist: null,
      newNum: null,
      form: {
        name: '',
        memo: '',
      },
      providerForm: {
        total: '',
        balance: '',
        name: '',
      }
    }
  },
  methods: {
    applyProviderAjax(items) {
        let providerUrl = this.providerUrl;
        return new Promise( (resolve, reject) =>{
            $.ajax({
              type:"POST",
              url: providerUrl+'resource',
              data: JSON.stringify(items),
              headers: {
                'userAgent': "Mozilla/5.0 (Windows NT 6.2; WOW64) AppleWebKit/537.36 (KHTML, like Gecko) Chrome/60.0.3095.5 Safari/537.36",
                'Access-Control-Allow-Origin': '*'
              },
              contentType: "application/json; charset=utf-8",
              dataType: "JSON",
              success(result) {
                resolve(result);
              },
              error(callback) {
                reject(callback);
              }
            })
        })
    },
    //
    applyProvider() {
      let items = this.providerForm
      let _this = this

      this.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          _this.applyProviderAjax(items).
          then( e => {
            console.log(e)
            this.providerForm.total = ''
            this.providerForm.balance = ''
            this.providerForm.name = ''
            this.dialogFormVisible3 = false
          })
          this.$message({
            type: 'success',
            message: '!'
          });
        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          });
        });
    },
    //alert
    infoProviderCost(row) {
      this.dialogFormVisible2 = true
      this.infoCostRow = row
    },
    //alert
    infoProviderAlert() {
      let _this = this
      this.$confirm(' ', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          _this.infoProviderDone()
          _this.dialogFormVisible2 = false
          this.$message({
            type: 'success',
            message: '!'
          });
        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          });
        });
    },
    cancelAlert() {
      this.dialogFormVisible2 = false
      this.loadProvider()
    },
    //ajax
    infoProviderDone() {
      let index = null
      let _this = this
      let providerUrl = this.providerUrl
      let balance = this.infoCostRow.balance
      if(this.sumExist == '/') {
         balance = Number(balance) - Number(this.newNum)
      }
      if(this.sumExist == '/') {
        balance = Number(balance) + Number(this.newNum)
      }
      this.infoCostRow.category = this.infoCostRow.category
      this.infoCostRow.balance = balance
      let infoCostRow = this.infoCostRow
      console.log(infoCostRow)
      $.ajax({
        type:"POST",
        url: providerUrl+'provider',
        data: JSON.stringify(infoCostRow),
        headers: {
          'userAgent': "Mozilla/5.0 (Windows NT 6.2; WOW64) AppleWebKit/537.36 (KHTML, like Gecko) Chrome/60.0.3095.5 Safari/537.36",
          'Access-Control-Allow-Origin': '*'
        },
        contentType: "application/json; charset=utf-8",
        dataType: "JSON",
        success(result) {
          _this.loadProvider()
          console.log(result)
        },
        error(result) {
          console.log(result)
        }
      })

    },
    //ajax
    deleteProvider(row) {
      let id = row.id
      let providerUrl = this.providerUrl
      let _this = this
        this.$confirm('?', '', {
         confirmButtonText: '',
         cancelButtonText: '',
         type: 'warning'
       }).then(() => {
         $.ajax({
           type:"POST",
           url: providerUrl+'provider/'+id,
           headers: {
             'userAgent': "Mozilla/5.0 (Windows NT 6.2; WOW64) AppleWebKit/537.36 (KHTML, like Gecko) Chrome/60.0.3095.5 Safari/537.36",
             'Access-Control-Allow-Origin': '*'
           },
           contentType: "application/json; charset=utf-8",
           dataType: "JSON",
           success(result) {
             _this.loadProvider()
             console.log(result)
           },
           error(callback) {
             alert(callback)
           }
         })
         this.$message({
           type: 'success',
           message: '!'
         });
       }).catch(() => {
         this.$message({
           type: 'info',
           message: ''
         });
       });

    },
    //ajax
    newProvider() {
      let form = this.form
      let providerUrl = this.providerUrl
      let _this = this
      console.log(form)
      form.balance = 0
      form.cost = 0
      this.$alert('', '', {
          confirmButtonText: '',
          callback: action => {
            this.dialogFormVisible = false

            $.ajax({
              type:"POST",
              url: providerUrl+'provider',
              data: JSON.stringify(form),
              headers: {
                'userAgent': "Mozilla/5.0 (Windows NT 6.2; WOW64) AppleWebKit/537.36 (KHTML, like Gecko) Chrome/60.0.3095.5 Safari/537.36",
                'Access-Control-Allow-Origin': '*'
              },
              contentType: "application/json; charset=utf-8",
              dataType: "JSON",
              success(result) {
                _this.loadProvider()
                form.name=''
                form.memo=''
                form.category=''
                console.log(result)
              },
              error(callback) {
                if(callback.code === 0) {
                  alert(',')
                }
              }
            })
            this.$message({
              type: 'info',
              message: ``
            });

          }
        });
    },
    //ajax
    getProvier() {
      let providerUrl = this.providerUrl;
      return new Promise( (resolve, reject) =>{
          $.ajax({
            type:"GET",
            url: providerUrl+'provider',
            headers: {
              'userAgent': "Mozilla/5.0 (Windows NT 6.2; WOW64) AppleWebKit/537.36 (KHTML, like Gecko) Chrome/60.0.3095.5 Safari/537.36",
              'Access-Control-Allow-Origin': '*'
            },
            contentType: "application/json; charset=utf-8",
            dataType: "JSON",
            success(result) {
              resolve(result);
            },
            error(callback) {
              reject(callback);
            }
          })
      })
    },
    getCateGory(c) {
      if(c === 0) return ''
      if(c === 1) return ''
    },
    //ajax
    loadProvider() {
      this.getProvier()
      .then( result => {
        console.log(result);
        this.providerData = result.value
      })
    },
  },
  created() {
    this.loadProvider()
  },
  components: {
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
.row > div{
  text-align: center;
  border: 0.1px solid rgba(100, 100, 100, 0.1);
}
.row{
  height:50px;
  line-height: 50px;
  margin-top:5px;
}
.data_body{
  cursor: pointer;
}
.data_body:hover{
  border: 0.1px solid rgba(100, 100, 100, 0.3);
}
.data_title{
  font-size: 18px;
}
.button_wrap{
  margin: 20px 0;
}

.grid-content {
    border-radius: 4px;
    min-height: 36px;
  }
.cell{
  text-align: center;
}
</style>



